//* FONCTION AFFICHER TABLEAU RESULTAT *//

function afficherTableauResultats(tableau, amperage, intensite, vitesseFil) {
	var resultatDiv = document.getElementById("resultats");

	resultatDiv.innerHTML = ""; // Nettoyer le contenu pr√©c√©dent

	var table = document.createElement("table");
	var tbody = document.createElement("tbody");

	tableau.forEach(function (rowData) {
		var row = document.createElement("tr");

		rowData.forEach(function (cellData) {
			var cell = document.createElement("td");
			cell.appendChild(document.createTextNode(cellData));
			row.appendChild(cell);
		});

		tbody.appendChild(row);
	});

	table.appendChild(tbody);
	resultatDiv.appendChild(table);

	// Afficher les r√©sultats pass√©s en param√®tres
	var infoDiv = document.createElement("div");
	infoDiv.innerHTML =
		//
		"Votre amp√©rage doit √™tre de : " +
		amperage.min +
		"A" +
		" et " +
		amperage.max +
		"A" +
		"<br>" +
		//
		"Votre intensit√© doit √™tre de : " +
		intensite.min +
		"A" +
		" et " +
		intensite.max +
		"A" +
		"<br>" +
		//
		"Votre vitesse d'avance doit √™tre de : " +
		vitesseFil.min +
		"A" +
		" et " +
		vitesseFil.max +
		"A";
	resultatDiv.appendChild(infoDiv);
}

module.exports = afficherTableauResultats;
